<template lang="pug">

.page
  .container
    el-row(type='flex', justify='center')
      h1 Áî®Êà∑Ê≥®ÂÜå

    el-row(type='flex', justify='center')
      el-col(:lg=12, :md=16, :sm=18, :xs=24)
        el-alert(
          title='ËØ∑Ê≥®ÊÑè, Âú®ÊµãËØïÊúüÈó¥ÊÇ®ÁöÑÊï∞ÊçÆÂèØËÉΩ‰ºöÂá∫Áé∞Èîô‰π±ÁîöËá≥‰∏¢Â§±ÁöÑÊÉÖÂÜµ. Â¶ÇÊûúÊÇ®Âú®ÊµãËØïÊúüÈó¥ÈÅáÂà∞‰∫Ü‰ªª‰ΩïÈóÆÈ¢ò, ÂèØ‰ª•Âú®È°µÈù¢Â∫ïÈÉ®ÊâæÂà∞ÊàëÁöÑËÅîÁ≥ªÊñπÂºè.'
          type='warning')

    el-row(type='flex', justify='center')
      el-col(:lg=12, :md=16, :sm=18, :xs=24)
        el-form(:model='formRegister', :rules='rules', ref='formRegister')
          el-form-item(prop='email', :error='fieldErr.email')
            el-input(v-model='formRegister.email',
                type='email',
                placeholder='Email',
                :autofocus='true',
                clearable)

          el-form-item(prop='username', :error='fieldErr.username')
            el-input(v-model='formRegister.username', placeholder='Áî®Êà∑Âêç', clearable)
          el-form-item(prop='password')
            el-input(v-model='formRegister.password', type='password', placeholder='ÂØÜÁ†Å', clearable)
          el-form-item(prop='agreement')
            el-checkbox(v-model='formRegister.agreement') ÊàëÂêåÊÑè

          el-form-item.buttons
            el-button.btn-register(type='primary', @click='onSubmit', :loading='btnLoading') Ê≥®ÂÜå
            el-button(type='text', @click=`$router.push({ name: 'Login' })`) Â∑≤ÊúâË¥¶Âè∑, ÂéªÁôªÈôÜ

</template>

<script>
import { validator } from '@/utils'

// Ëß£ÊûÑÂæÖÈ™åËØÅÂ≠óÊÆµ
const { email, username, password, agreement } = validator.fields

export default {
  data () {
    return {
      formRegister: {
        email: 'testuser@mutoe.com',
        username: 'testuser',
        password: '123456',
        agreement: true,
      },
      rules: { email, username, password, agreement },
      fieldErr: {},
      btnLoading: false,
    }
  },
  methods: {
    onSubmit () {
      // Ë°®ÂçïÈ™åËØÅ
      this.$refs['formRegister'].validate(valid => {
        if (!valid) return
        const postData = Object.assign({}, this.formRegister)
        this.postForm(postData)
      })
    },
    postForm (postData) {
      this.btnLoading = true
      this.fieldErr = {}
      this.$http.post('/api/auth/register', postData)
        .then(this.onSuccess)
        .catch(this.onError)
        .finally(() => {
          this.btnLoading = false
        })
    },
    onSuccess (res) {
      const { token } = res.data
      this.$message.success('Ê≥®ÂÜåÊàêÂäü')
      this.$store.dispatch('Login', { email, token })
      this.$http.defaults.headers.common['Authorization'] = 'Bearer ' + token
      this.$router.replace('/')
    },
    onError (err) {
      console.warn(err.response)
      const { status, data } = err.response
      let message = ''
      switch (status) {
        case 500: message = 'ÊúçÂä°Âô®Âá∫ÈîôÂï¶'; break
        case 400:
          if (data.type === 'field_exist') {
            this.fieldErr[data.field] = data.message
          }
          message = data.message
          break
      }
      this.$message.error({
        message: `üôÑ ${message}`,
        iconClass: 'none',
        customClass: 'el-message--error',
      })
    },
  },
}

</script>

