        :on-change='onUploadChange'
        :on-preview='handlePictureCardPreview'
        :on-success='onUploadSuccess'
        :on-error='onUploadError')
      i.el-icon-upload

    .el-upload__text
      | Â∞ÜÊñá‰ª∂ÊãñÂà∞ÊñπÊ°ÜÂÜÖÔºåÊàñÁÇπÂáªÊñπÊ°Ü‰∏ä‰º† #[br]
      | ÊØèÂº†ÂõæÁâáÊúÄÂ§ß 8MÔºåÊîØÊåÅ JPEG GIF PNG #[br]
      | ÊØèÊ¨°ÊúÄÂ§ö‰∏ä‰º† {{ upload.limit }} Âº†, ÂÖ± 30M ‰ª•‰∏ã #[br]

  el-row(type='flex', justify='center')
