          el-input(type='textarea', v-model='form.description', :autosize="{ minRows: 4}")
        el-form-item(label='ÂÖ¨ÂºÄËåÉÂõ¥')
          el-radio-group(v-model='form.scope', size='small')
            el-radio-button(label='all') ÂÖ¨ÂºÄ
            el-radio-button(label='friends', disabled) ‰ªÖÂ•ΩÂèã(Â∞öÊú™ÂºÄÊîæÂ•ΩÂèãÂäüËÉΩ)

        el-form-item.declare
          | ‰ª•‰∏ã‰ΩúÂìÅÁ¶ÅÊ≠¢ÊäïÁ®ø„ÄÇËØ∑Âú®ÊäïÁ®ø‰πãÂâçËøõË°åÁ°ÆËÆ§
          ul
            li Âê´ÊúâÊ≥ïÂæãËßÑÂÆöÁ¶ÅÊ≠¢ÂÖ¨ÂºÄ‰º†Êí≠ÁöÑÂÜÖÂÆπ.
            li Âê´ÊúâÈú≤È™®„ÄÅËØ±ÊÉë„ÄÅÊïôÂîÜÁ≠âÂÜÖÂÆπÁöÑÂõæÁâáÊàñÊñáÂ≠ó.
            li Êï£Â∏ÉË∞£Ë®Ä„ÄÅÂπøÂëä„ÄÅËØΩË∞§Á≠âÂÜÖÂÆπÁöÑÂõæÁâáÊàñÊñáÂ≠ó.
          | ËøùÂèç‰ΩúÂìÅÊäïÁ®øÂà©Áî®ËßÑÂàôÁöÑÁî®Êà∑ÔºåÂ∞Ü‰ºöË¢´ÂÅúÊ≠¢ÊäïÁ®ø‰ΩúÂìÅÂÖ¨ÂºÄÔºåÂÅúÊ≠¢Ë¥¶Âè∑‰ΩøÁî®„ÄÇ #[br]
