<script>
export default {
  data () {
    return {
      oss: {
        key: '',
        dirPath: '',
        OSSAccessKeyId: '',
        policy: '',
        Signature: '',
        host: '',
        success_action_status: 200,
        callback: '',
      },
      upload: {
        accept: '',
        limit: 100,
        list: [],
        listType: 'picture-card',
      },
      dialog: {
        imageUrl: '',
        title: '',
        visible: false,
      },
      form: {
        title: '',
        description: '',
        scope: 'all',
      },
    }
  },
  mounted () {
    // ËØ∑Ê±Ç oss Á≠æÂêç
    this.ossSign()
  },
  methods: {
    // ËØ∑Ê±Ç oss Á≠æÂêç
    ossSign () {
      this.$http.get('/api/auth/ossSign')
        .then(res => Object.assign(this.oss, res.data))
        .catch(this.onError)
    },

    // ‰∏ä‰º†ÂâçÊ£ÄÊü•
    beforeUpload (file) {
      if (file.type.split('/')[0] !== 'image') {
        this.$message.error('Êñá‰ª∂Á±ªÂûãÈîôËØØ')
        return false
      }
      if (file.size > 8 * 1024 * 1024) {
        this.$message.error('Âçï‰∏™Êñá‰ª∂Ë∂ÖËøá 8 M')
        return false
      }

      // ÁîüÊàêÊñá‰ª∂Âêç
      const fileExt = file.name.split('.').pop()
      const randomString = (new Date().getTime() + Math.random()).toString(36).replace(/\./, '')

      this.oss.key = this.oss.dirPath + randomString + '.' + fileExt
      return true
    },

    onUploadSuccess (res, file, fileList) {
      const filePath = res.result.filename
      file.path = filePath
      file.filename = filePath.split('/').pop()
    },
    onUploadError (err, file, fileist) {
      console.warn(err)
      this.$message.error('Âá∫Èîô‰∫Ü')
    },

    // Êõ¥Êñ∞Êñá‰ª∂ÂàóË°®
    onUploadChange (file, fileList) {
      this.upload.list = fileList
    },

    // È¢ÑËßà
    handlePictureCardPreview (file) {
      this.dialog.imageUrl = this.oss.host + file.path
      this.dialog.title = file.name
      this.dialog.visible = true
    },

    // Êèê‰∫§Ë°®Âçï
    submit () {
      if (this.upload.list.length === 0) return this.$message.error('ËØ∑Ëá≥Â∞ë‰∏ä‰º†‰∏ÄÂº†ÂõæÁâá')
      const postData = Object.assign({}, this.form, { list: this.upload.list })
      this.$http.post('/api/image', postData)
        .then(this.onSubmitSuccess)
        .catch(this.onError)
    },
    onSubmitSuccess (res) {
      const { image } = res.data
      this.$message.success(`ÂàõÂª∫ÊàêÂäü, Âç≥Â∞ÜË∑≥ËΩ¨...`)
      setTimeout(() => {
        this.$router.push({ name: 'ImageRead', params: { id: image._id } })
      }, 1200)
    },
    onError (err) {
      console.warn(err)
    },
  },
}
</script>

